<template>
	

<div class='uploadVoucher_page'>
	
	<header-top title='' back></header-top>
	<div class="fixed-top-div"></div>

    <div class="content shangchuanhuikuan">

        <form action="" class="layui-form">

            <div class="tips1">
                
            </div>

            <div class="form-group">
                <label for="name"></label>
                <input type="text" id="name" lay-verify="name" v-model:value="uploadInfo.p_name" placeholder="">
            </div>
            <div class="form-group">
                <label for="id-card"></label>
                <input type="text" lay-verify="money" v-model:value="uploadInfo.p_money" id="id-card" placeholder="">
            </div>
            <div class="form-group">
                <label for="idCard"></label>
                <input type="text" id="idCard" lay-verify="b_name"  v-model:value="uploadInfo.b_name"  placeholder="">
            </div>
            <div class="form-group">
                <label for="address"></label>
                <input type="text" id="address" lay-verify="b_no"  v-model:value="uploadInfo.b_no" placeholder="">
            </div>
            <div class="form-group">
                <label for="phone"></label>
                <input type="text" id="phone" lay-verify="serial"  v-model:value="uploadInfo.serial" placeholder="">
            </div>
            <div class="form-group">
                <label for="area"></label>
                <input type="text" id="area" lay-verify="phone"  v-model:value="uploadInfo.phone" placeholder="">
            </div>

            <div class="form-img">

<!--                 <div v-show="false">
                    <img v-if="images.length>0" >

                    <span v-if="images.length>0" class="del">x</span>
                    <img v-else src="../../../../assets/imgs/center/icon_pinzheng.png" >
                    <input type="file" lay-verify="photo" name="file" id="photo" class="hide"/>
                </div> -->



              <!--  -->
              <drop-zone id="voucherUpload" ref='uploadImg' useFontAwesome="false" methods='post' 
                :autoProcessQueue=false
                :useFontAwesome=false
                :maxFileSizeInMB=10

                :maxNumberOfFiles=1
                :language="{dictRemoveFile:'',dictCancelUpload:'',dictFileTooBig:'{{filesize}}Mib{{maxFilesize}}Mib'}"

                :headers="{cache:false,processData:false,contentType:false}"
                acceptedFileTypes="image/*" 
                url="http://192.168.0.123:8080/beta6.3/loadfile" 
                v-bind:preview-template="template"
                v-on:vdropzone-success="showSuccess"
                v-on:vdropzone-removed-file="removeImg"

              >

                <div class="dz-default dz-message"><img style="height:5rem;" src="../../../../assets/imgs/center/icon_pinzheng.png" ></div>

                  <!-- Optional parameters if any! -->
                  <!-- <input type="hidden" name="token" value="xxx"> -->
              </drop-zone>



            </div>

            <div class="form-btn">
                <button class="btn btn-button" @click.prevent="submitPhoto"></button>
            </div>
        </form>

    </div>


</div>




</template>

<script>

import headerTop from '@/components/header/header'	
import dropZone from 'vue2-dropzone'
export default{

	components:{
		headerTop,
        dropZone
	},
	data(){
		return{

            uploadInfo:{
                vip:this.$store.getters.getVip,//
                p_name:"",//
                p_money:"",//
                b_name:"",//
                b_no:"",//
                serial:"",//
                phone:"",//
                photo:"",//
                otherNo:this.$route.query.otherNo,// 
            },
		}
	},
	methods:{


        //
        submitForm(){

            this.$http.post("/GnRecharge",this.uploadInfo).then((res)=>{

                console.log(res.data);

                if(res.data.code==0){
                    layer.open({
                        skin:'mes',
                        content:'!',
                        time:1.5,
                        btn:'',
                    });
                }

            })




        },

        submitPhoto(){

            //

            //
            this.$refs.uploadImg.processQueue();
        },

        //
        showSuccess(file,res){

            console.log(res)

            if(res.code==0){
                this.uploadInfo.photo = res.response;
                //
                this.submitForm();
            }

        },
        //
        removeImg(){

        },
        //
        template() {
              return `
                      <div class="dz-preview dz-file-preview" style="width:100%;height:100%;padding:.1rem;margin:0;">
                          <div class="dz-image" style="width:100%;height:100%;">
                              <img data-dz-thumbnail />
                          </div>
                          <div class="dz-progress">
                            <span class="dz-upload" data-dz-uploadprogress></span>
                          </div>

                          <div class="dz-error-message" style="top:inherit;"><span data-dz-errormessage></span></div>

                          <div class="dz-details">
                            <div class="dz-size"><span data-dz-size></span></div>
                            <div class="dz-filename"><span data-dz-name></span></div>
                          </div>
                      </div>
                  `;
          },





	}


}

</script>


<style lang='less'>
	
@import '../../../../assets/css/variable';




#voucherUpload{
    min-height: inherit;
    padding:0;
    width:100%;

    .dz-message{
        margin:.2rem auto;
    }
    .dz-remove{
        opacity: 1;
        background: #1880D8;

        left:0;
        right:0;
        margin:auto;
        width:3rem;
        font-size:.6rem;
        bottom:0;

    }
    .dz-image{
        display: flex;
        justify-content: center;

        img{
            max-height: 8rem;
        }
    }
}



.uploadVoucher_page{
	.fixed();
	z-index: 9999;

}


/*//*/
.shangchuanhuikuan{

  background: #fff;
  .tips1{
    background: #EAEAEA;
    color:#f00;
    padding:10/@root-font-size 9/@root-font-size;
    font-size:.8rem;
  }

  .border-bottom{
    border-bottom:1px solid #E4E4E4;
  }


  .form-group{
    .border-bottom;
    padding:12/@root-font-size 12/@root-font-size;

    display: flex;
    label{
      justify-content: flex-start;
      width:35%;
      font-size:.6rem;

    }
    input{
      width:65%;
      font-size:.6rem;
    }

  }

  .form-img{
    /*position: relative;*/
    width:100%;
    display: inline-block;
    padding:5/@root-font-size;
    text-align: center;



/*    img{
      max-height:90/@root-font-size;
      border-radius:4px;
      border:1px solid #dddddd;

    }*/
    input{
      opacity:0;
      width:100%;
      height:100%;
      left:0;
      top:0;
      position: absolute;


    }

    span.del{
      position: absolute;
      font-weight: bold;
      top:5/@root-font-size;
      font-size:.8rem;
      right:-20px;
    }
  }

  .form-btn{
    padding:0/@root-font-size 9/@root-font-size;

    .btn{
      width:100%;
      line-height:35/@root-font-size;
      background: #389E3A;
      color:#fff;
      border-radius:4px;
      font-size:.7rem;
    }
  }

}

</style>








